body {
}
a {
  display: block;
}




.slogan-div {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 5%;
}

.slogan-text {
  color: darkgray;
  font-size: 35px;
  text-transform: uppercase;
}

.header-pad {
  padding-right: 10%;
  padding-left: 10%;
}
 
.header-bg {
  background-color: rgba(75, 72, 91, 0.7);
}

.header-bg--1 {
  background-color: rgba(75, 72, 91, 1);
}

.info--index {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.info--about {
  margin-top: 100px;
  margin-bottom: 135px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.info--about-list {
  align-self: flex-start;
  margin-left: 20px;
}

.info--about-ml {
  align-self: flex-start;
  margin-left: 40px;
  font-weight: 600;
}

// Make menu color lighter
// .color-header a {
//   color: rgba(255,255,255,.8) !important;  
// }

// .color-header .active a {
//   color: rgba(255,255,255,1) !important;
// }

                                    // START gallery


.gallery--gal-style {
  margin-top: 75px;
  margin-bottom: 135px;
  a {
    color: black;
    text-align: center;
  }
}                              

.thumb img {
  -webkit-filter: grayscale(0);
  filter: none;
  border-radius: 5px;
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 5px;
}

.thumb--border-none img {
  border: none;
}

.thumb img:hover {
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
}

.thumb {
  padding: 5px;
}
                                  // END gallery

.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 20px;
  margin-top: 10px;
  background-color: rgba(75, 72, 91, 1);
  color: whitesmoke;
  a {
      text-decoration: none;
      color: whitesmoke;
  }
}



