//   // margin-left: auto;
// }

.flex__center {
  display: flex;
  flex-direction: column;
  // justify-content: center;
  align-items: center;
  text-align: center;
}

@media (max-width: 770px) {
  .about-us__bottom-unfixed {
    position: relative;
  }
}

@media (max-width: 721px) {
  .gallery--gal-style {
    margin-bottom: 30px;
  }
}

@media (max-width: 920px) {
  .gallery__bottom-unfixed {
    position: relative;
  }
}




@media (max-width: 992px) {
  .md-outside {
    display: flex;
    flex-direction: row;
    justify-content: right;
    // .content-right {
    //   // .info--index { 
    //   //   display: flex;
    //   //   flex-direction: column;
    //   //   justify-content: center;        
    //   // }
    // }
  }
  .info--index { 
    display: flex;
    flex-direction: column;
    justify-content: center;        
  }
  // .info--about {
  //   margin-bottom: 420px;
  // }

  .slogan-bg {
    background: url("../image/background-top.jpg") no-repeat scroll center top #000;
    background-size: cover;
    width: 100%;
    height: 235px;
  }

  .flex__right {
    display: flex;
    justify-content: center;
  }
  .flex__left {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    text-align: center;
  }
}


@media (min-width: 993px) {
  .slogan-bg {
    background: url("../image/background-top.jpg") no-repeat scroll center top #000;
    background-size: cover;
    width: 100%;
    min-height: 400px;
  }
  .flex__right {
    display: flex;
    justify-content: right;
    a {
      margin-left: auto;
    }
  }
  .flex__left {
    display: flex;
    flex-direction: column;
    justify-content: start;    
  }
  
}